import React from 'react';
import {Link} from 'react-router-dom';
import classNames from 'classnames';

class SideMenu extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            menu: [
                {
                    title: 'recruitment',
                    flag: false,
                },
            ],
        };
    }

    openMenu = (index) => {
        console.log(index);
        // console.log(this.props.sideMenu);
    };

    render() {
        if (!this.props.sideMenu) {
            return;
        }

        return (
            <div className="sideMenuBox">
                <h2>Menu</h2>
                <ul className="sideMenuBoxListBox">
                    {
                        this.props.sideMenu.map((data, index) => {
                            return (
                                <li key={index}>
                                    <div onClick={() => {
                                        this.openMenu(index);
                                    }} className="sideMenuBoxListBox_title">{data.title}</div>
                                    <ul className="sideMenuBoxListBox_childBox">
                                        {
                                            this.props.sideMenu[index].child.map((childData, childIndex) => {
                                                return (
                                                    <li key={childIndex}>
                                                        {childData.title}
                                                    </li>
                                                );
                                            })
                                        }
                                    </ul>
                                </li>
                            );
                        })
                    }
                </ul>
            </div>
        );
    }
}

export default SideMenu;



